import { useState } from "react";
import { useHasPermission } from "../../api/operations";
import { useMutationDeleteStaff, useMutationResetStaffPassword, useStaff } from "../../api/staff";
import Layout from "../../components/Layout";
import { useTitle } from "../../utils/useTtile";
import styles from "./styles.module.css"
import CreateStaffForm from "../../components/create/CreateStaffForm";
import PopupConfirmDeletion from "../../components/PopupConfirmDeletion";
import Spinner from "../../components/Spinner";
import displayError from "../../utils/displayError";

export default function StaffPage()
{
	useTitle("");
	const [createFormOpen, setCreateFormOpen] = useState(false);
	const [deletionStaff, setDeletionStaff] = useState(-1);
	const [resetPasswordStaff, setResetPasswordStaff] = useState(-1);
	const staff = useStaff();

	return (
		<Layout centeredPage gap={8}>
			<h1></h1>
			{staff.isLoading && <Spinner/>}
			{displayError(staff)}
			{staff.data?.length == 0 && <h2>   </h2>}
			{staff.data?.map(v => <div className={styles.user} key={v.id}>
				<input type="checkbox" className={styles.toggleInp} id={`user${v.id}`} />
				<label className={styles.title} htmlFor={`user${v.id}`}>
					<div>{v.name}</div>
					<div>
						<span>{v.roles.join(", ")}</span>
						<div className={styles.toggle}></div>
					</div>
				</label>
				<div className={styles.userInfo}>
					<div>: {v.login}</div>
					<div>
						<span>: </span>
						{v.password || <button className="button button_light button_small" onClick={() => setResetPasswordStaff(v.id)}> </button>}
					</div>
					<div className="space_between">
						<span></span>
						<button className="button button_danger button_small" onClick={() => setDeletionStaff(v.id)}></button>
					</div>
				</div>
			</div>)}
			{useHasPermission("add_staff") &&
				<div className="space_between">
					<span></span>
					<button className="button" onClick={() => setCreateFormOpen(true)}></button>
				</div>
			}
			<CreateStaffForm open={createFormOpen} close={() => setCreateFormOpen(false)} />
			<PopupConfirmDeletion itemId={deletionStaff} title=" " mutationFn={useMutationDeleteStaff} open={deletionStaff >= 0} close={() => setDeletionStaff(-1)} />
			<PopupConfirmDeletion itemId={resetPasswordStaff} title="  " mutationFn={useMutationResetStaffPassword} open={resetPasswordStaff >= 0} close={() => setResetPasswordStaff(-1)} />
		</Layout>
	);
}
